.ProductDetails{
    background-color:white;
    width: 100vw;
    max-width: 100%;
    box-sizing: border-box;
    padding: 6vmax;
    display: flex;
}

/* .ProductDetails >div:first-child{
width: 50%;
margin: auto;
} */


.ProductDetails > div{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    /* align-items: center; */
    padding: 2vmax;
    box-sizing: border-box;
    border: 1px solid white;
}

.ProductDetails > div:last-child{
    align-items: flex-start;
}

.CarouselImage{
    width: 20vmax;
    height: 20vmax;
    object-fit: scale-down;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.detailsblock-1 > h2{
    color: rgb(54, 54, 54);
    font: 600 1.6vmax "Roboto";
}

.detailsblock-1 > p{
    color: rgba(54, 54, 54,0.582);
    font: 200 0.6vmax cursive;
}

.detailsblock-2{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 70%;
    padding: 1vmax 0;
}

.detailsblock-2-span{
    color: rgba(54, 54, 54, 0.582);
    font: 200 0.8vmax cursive;
}

.detailsblock-3{
    width: 70%;
}

.detailsblock-3 > h1{
    color: rgba(17, 17, 17, 0.795);
    font: 400 1.8vmax "Franklin Gothic Medium";
    margin: 1vmax 0;
}

.detailsblock-3-1{
    display: flex;
    align-items: center;

}

.detailsblock-3-1-1 > button{
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;

}

.detailsblock-3-1-1 > button:hover{
    background-color: rgba(0, 0, 0, 0.767);
}

.detailsblock-3-1-1 > input{
    border: none;
    padding: 0.5vmax;
    width: 1vmax;
    text-align: center;
    outline: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0,0, 0, 0.74);
}

.detailsblock-3-1 > button:last-child{
    border: none;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 0.7vmax "Roboto";
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.detailsblock-3-1 > button:last-child:hover{
    background-color: rgb(214, 84, 61);
}

.detailsblock-3 > p{
    border-top: 1px solid rgba(0 , 0, 0, 0.205);
    border-bottom: 1px solid rgba(0 , 0, 0, 0.205);
    padding: 1vmax 0;
    color: rgba(0 , 0, 0, 0.651);
    font: 400 1vmax "Roboto";
    margin: 1vmax 0;
}

.detailsblock-4{
    color: rgba(0 , 0, 0, 0.897);
    font: 500 1.2vmax sans-serif;
}

.detailsblock-4 > p{
    color: rgba(0 , 0, 0, 0.534);
}


.submitReview{
    border: none;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 0.7vmax "Roboto";
    border-radius: 20px;
    padding: 0.6vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.submitReview:hover{
    background-color: rgb(197, 68, 45);
    transform: scale(1.1);
}

.submitDialog{
    display: flex;
    flex-direction: column;
}

.submitDialogTextArea{
    border: 1px solid rgba(0 , 0, 0, 0.082);
    margin: 1vmax 0;
    outline: none;
    padding: 1rem;
    font: 300 1rem "Roboto";
}


.reviewsHeading{
    color: #000000be;
    font: 500 1.4vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0 , 0, 0, 0.226);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    margin-bottom: 4vmax;
}

.reviews{
    display: flex;
    overflow: auto;
}

.reviewCard{
    flex: none;
    box-shadow: 0 0 5px rgba(0 , 0, 0, 0.226);
    border: 1px solid rgba(56, 56, 56, 0.116);
    width: 30vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1vmax;
    padding: 3vmax;
}

.reviewCard > img{
    width: 5vmax;
}

.reviewCard > p{
    color: rgba(0, 0, 0, 0.836);
    font: 600 0.9vmax "Roboto";
}

.reviewCard-span{
    color: rgba(0, 0, 0, 0.445);
    font: 300 0.8vmax cursive;
}

.noReviews{
    font: 400 1.3vmax "Gill Sans";
    text-align: center;
    color: rgba(0 , 0, 0, 0.548);
}


@media screen and (max-width: 600px) {
    .ProductDetails{
        flex-direction: column;
        height: unset;
    }
    .ProductDetails > div:last-child{
        align-items: center;
    }
    .detailsblock-1 > h2{
        text-align: center;
        font-size: 2.8vmax;
    }
    .detailsblock-1 > p{
        text-align: center;
        font-size: 1vmax;
    }
    .detailsblock-2{
        justify-content: center;
    }
    .detailsblock-2 > span{
        font-size: 1.5vmax;
    }
    .detailsblock-3 > h1{
        font: 700 3vmax "Franklin Gothic Medim";
        text-align: center;
    }
    .detailsblock-3-1{
        flex-direction: column;
    }
    .detailsblock-3-1-1{
        padding: 2vmax 0;
    }
    .detailsblock-3-1-1 > button{
        padding: 1.2vmax;
        width: 4vmax;
    }
    .detailsblock-3-1-1 > input{
        padding: 1.5vmax;
        width: 3vmax;
        font: 400 1.8vmax "Roboto";
    }
    .detailsblock-3-1 > button:last-child{
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }
    .detailsblock-3 > p{
        padding: 2.5vmax 0;
        text-align: center;
        font: 400 2vmax "Roboto";
    }
    .detailsblock-4{
        font: 500 2.5vmax sans-serif;
    }
    .detailsblock-4 > p{
        font: 300 1.8vmax sans-serif;
    }
    .submitReview{
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }

    .reviewCard > p{
        font: 600 3vw "Roboto";
    }

    .reviewCard-span{
        font: 300 5vw cursive;
    }

}